<template>
	<view class="">
		<form class="">
			<view class="username">
				请输入用户名: <input style="padding-top: 20rpx;" v-model="username" type="text" value="" />
			</view>
			<view class="password">
				请输入密码： <input style="padding-top: 20rpx;" v-model="password" type="text" value="" />
			</view>
			<view class="login">
				<button type="default" @click="check()">确认</button>
			</view>
		</form>
	</view>
</template>

<script>
	import axios from 'axios'
	import Qs from 'qs'
	export default {
		data() {
			return {
				username: '',
				password: ''
			}
		},
		methods: {
			check() {
				uni.request({
					url: 'http://localhost:8001/login',
					method: 'POST',
					data: Qs.stringify({
						username: this.username,
						password: this.password
					}),
					success: (res) => {
						// console.log(res.data);
						// console.log(res.data.data.user.username);
						// console.log(res.data.data.user.user.avatar);
						if (res.data.code === 200) {
							window.sessionStorage.setItem('userId', res.data.data.user.user.id);
							window.sessionStorage.setItem('username', res.data.data.user.username);
							window.sessionStorage.setItem('avatar', res.data.data.user.user.avatar);
							this.$store.commit('setUserInfo', {
								username: window.sessionStorage.getItem('username'),
								avatar: window.sessionStorage.getItem('avatar')
							})
							console.log(this.$store.state.user.username)
							uni.switchTab({
								url: '../index/index'
							})
						} else {
							uni.showToast({
								title: res.data.message,
								duration: 2000
							})
						}

					},
					header: {
						'Content-Type': 'application/x-www-form-urlencoded',
					},
					fail: (res) => {

					}
				})
			}
		}
	}
</script>

<style scoped lang="scss">
	.username {
		width: 600rpx;
		margin: 40rpx auto;
		border-bottom: 2rpx solid #000000;
	}

	.password {
		width: 600rpx;
		margin: 0rpx auto;
		border-bottom: 2rpx solid #000000;
	}

	.login {
		button {
			width: 200rpx;
			height: 80rpx;
			margin-top: 20rpx;
			color: red;
			background-color: white;
			border: 2rpx solid red;
			border-radius: 20rpx;
			text-align: center;
		}

	}
</style>
